<script>
import axios from "axios";
import Bulle from "@/views/bulle";

export default {
  components: { Bulle },
  data() {
    return {
      userSelected: "Her",
      currentList: [],
      monthSelected: 0,
      firstDay: undefined,
      currentDescription: undefined,
      currentStatus: undefined,
      selectedIndex: undefined,
      score: 0,
      trainedDayNumber: 0,
      scoreClass: undefined,
      soloNumber: 0,
      superNumber: 0,
      noneNumber: 0,
      leftEnterAnimation: false,
      rightEnterAnimation: false
    };
  },
  methods: {
    accountSelect(user) {
      document.getElementById("bulle").style = "display:none";
      this.userSelected = user;
      this.loadMonth();
    },
    monthChosen() {
      if (this.monthSelected == 0) return "Janvier";
      if (this.monthSelected == 1) return "Février";
      if (this.monthSelected == 2) return "Mars";
      if (this.monthSelected == 3) return "Avril";
      if (this.monthSelected == 4) return "Mai";
      if (this.monthSelected == 5) return "Juin";
      if (this.monthSelected == 6) return "Juillet";
      if (this.monthSelected == 7) return "Août";
      if (this.monthSelected == 8) return "Septembre";
      if (this.monthSelected == 9) return "Octobre";
      if (this.monthSelected == 10) return "Novembre";
      if (this.monthSelected == 11) return "Décembre";
    },
    previousMonth() {
      if (this.monthSelected > 0) {
        this.leftEnterAnimation = true;
        this.monthSelected--;
        this.loadMonth();
        localStorage.month = this.monthSelected;
        setTimeout(() => {
          this.leftEnterAnimation = false;
        }, 500);
      }
    },
    nextMonth() {
      if (this.monthSelected < 11) {
        this.rightEnterAnimation = true;
        this.monthSelected++;
        this.loadMonth();
        localStorage.month = this.monthSelected;
        setTimeout(() => {
          this.rightEnterAnimation = false;
        }, 500);
      }
    },
    selectOne(number) {
      this.currentDescription = undefined;
      this.currentStatus = undefined;
      const bulle = document.getElementById("bulle");
      this.selectedIndex = number - this.firstDay;
      this.currentDescription = this.currentList[
        this.selectedIndex
      ].description;
      this.currentStatus = this.currentList[this.selectedIndex].status;
      bulle.style = "display:block";
    },
    async deleteBulle() {
      document.getElementById("bulle").style = "display:none;";
      this.currentList[this.selectedIndex].status = this.currentStatus;
      this.currentList[
        this.selectedIndex
      ].description = this.currentDescription;
      await axios.post(`${process.env.VUE_APP_API_URL}/update`, {
        description: this.currentDescription ? this.currentDescription : "",
        status: this.currentStatus ? this.currentStatus : "",
        id: this.currentList[this.selectedIndex].id
      });
      this.asssertScore();
    },
    async loadMonth() {
      this.currentList = [];
      const { data } = await axios.post(process.env.VUE_APP_API_URL, {
        month: this.monthSelected,
        user: this.userSelected == "Him" ? "him" : "her"
      });

      this.firstDay = data[0].weekDay;
      if (this.firstDay == 0) {
        this.firstDay = 7;
      }
      data.forEach(element => {
        if (element.user == "her" && this.userSelected == "Her") {
          this.currentList.push(element);
        }
        if (element.user == "him" && this.userSelected == "Him") {
          this.currentList.push(element);
        }
      });
      this.asssertScore();
    },
    asssertScore() {
      this.score = 0;
      this.trainedDayNumber = 0;
      this.soloNumber = 0;
      this.superNumber = 0;
      this.noneNumber = 0;
      this.currentList.forEach(element => {
        if (element.status == "Club") {
          this.score += 1;
          this.soloNumber += 1;
        }
        if (element.status == "Solo") {
          this.score += 1;
          this.soloNumber += 1;
        }
        if (element.status == "Super") {
          this.score += 2;
          this.superNumber += 1;
        }
        if (element.status == "Rien fait") {
          this.score += -1;
          this.noneNumber += 1;
        }
      });
      this.currentList.forEach(element => {
        if (element.status != "" && element.status != undefined) {
          this.trainedDayNumber += 1;
        }
      });
      this.scoreClass = this.assertScoreClass();
    },
    assertNumber(number) {
      const dayList = this.currentList.map(x => {
        return x.dayNumber;
      });
      const maxDayNumber = Math.max(...dayList);
      if (
        number - this.firstDay >= 0 &&
        number - this.firstDay < maxDayNumber
      ) {
        return { number: number - this.firstDay + 1, style: `` };
      } else {
        return { number: undefined, style: "display:none" };
      }
    },
    assertScoreClass() {
      if (this.score >= this.trainedDayNumber * 0.9) {
        return "is-success";
      } else {
        if (this.score >= this.trainedDayNumber * 0.8) {
          return "is-info";
        } else {
          if (this.score > this.trainedDayNumber * 0.6) {
            return "is-warning";
          } else {
            return "is-danger";
          }
        }
      }
    },
    assertClass(number) {
      const index = number - this.firstDay;
      const elementChecked = this.currentList[index];
      if (index >= 0 && index < this.currentList.length) {
        if (elementChecked.status == "Club") {
          return "is-primary";
        }
        if (elementChecked.status == "Solo") {
          return "is-info";
        }
        if (elementChecked.status == "Joker") {
          return "is-warning";
        }
        if (elementChecked.status == "Super") {
          return "is-success";
        }
        if (elementChecked.status == "Rien fait") {
          return "is-danger";
        }
      }
      return;
    },
    showDetails() {
      alert(`On est le ${this.trainedDayNumber}e jour du mois et tu as fait : \n
      ${this.soloNumber} fois du sport classique \n
      ${this.superNumber} fois du sport de façon intense \n
      ${this.noneNumber} fois rien du tout \n
      Tu as ${
        this.trainedDayNumber
          ? Math.floor((this.score / this.trainedDayNumber) * 100)
          : 0
      }% des points max`);
    }
  },
  mounted() {
    if (localStorage.month) {
      this.monthSelected = localStorage.month;
    }
    this.loadMonth();
  },
  computed: {
    statusChosen() {
      if (this.status == "Solo") {
        return "is-info";
      }
      if (this.status == "Club") {
        return "is-primary";
      }
      if (this.status == "Joker") {
        return "is-warning";
      }
      if (this.status == "Success") {
        return "is-warning";
      }
      if (this.status == "Rien fait") {
        return "is-danger";
      }
      return null;
    }
  }
};
