<template>
  <section>
    <article
      class="message bulle"
      v-on:keyCodes.enter="validateDescription"
      :class="statusChosen"
    >
      <div class="message-header">
        <p>Qu'as-fait ce jour là ?</p>
        <button
          class="delete"
          aria-label="delete"
          @click="validateDescription"
        ></button>
      </div>
      <div class="message-body">
        <p class="label-bulle">Sport</p>
        <div class="week">
          <b-button @click="chooseStatus('Club')" class="text-bulle is-primary"
            >Club</b-button
          >
          <b-button @click="chooseStatus('Solo')" class="text-bulle is-info"
            >Solo</b-button
          >
          <b-button @click="chooseStatus('Super')" class="text-bulle is-success"
            >Super</b-button
          >
          <b-button @click="chooseStatus('Joker')" class="text-bulle is-warning"
            >Joker</b-button
          >
          <b-button
            @click="chooseStatus('Rien fait')"
            class="text-bulle is-danger"
            >Rien fait</b-button
          >
        </div>
        <p class="label-bulle">Description</p>
        <textarea
          v-on:keyup.enter="validateDescription"
          class="description"
          v-model="description"
          placeholder="Ecrire la description du sport de la journée ou du joker..."
        ></textarea>
        <b-button class="text-bulle" @click="reset"
          ><i class="mdi mdi-undo-variant"></i
        ></b-button>
      </div>
    </article>
  </section>
</template>

<script>
export default {
  props: ["status", "description"],
  data() {
    return {};
  },
  methods: {
    reset() {
      this.status = undefined;
      this.description = undefined;
    },
    validateDescription() {
      this.$parent.currentDescription = this.description;
      this.$parent.currentStatus = this.status;
      this.$parent.deleteBulle();
    },
    chooseStatus(status) {
      this.status = status;
    }
  },
  computed: {
    statusChosen() {
      if (this.status == "Solo") {
        return "is-info";
      }
      if (this.status == "Club") {
        return "is-primary";
      }
      if (this.status == "Joker") {
        return "is-warning";
      }
      if (this.status == "Super") {
        return "is-success";
      }
      if (this.status == "Rien fait") {
        return "is-danger";
      }
      return null;
    }
  }
};
</script>
